import React, { useState } from 'react';
import { connect } from 'react-redux';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText
} from 'reactstrap';
import { Sticky } from 'react-sticky';

const renderContent = (props, flag) => {
  if(flag) {
  switch(props.auth) {
      case null:
          return ;
      case false:
          return ;
      default:
          return [
              <li key = "3" style={{  color: 'grey'}}>
                    <a href = "/api/addcredits" style={{ margin: '0px 8px', color: 'grey'}}><div>Add Credits</div></a>
              </li>,
              <li key = "2" style={{ color: 'grey'}}>
                <a href = "/surveys" style={{ margin: '0px 8px', color: 'grey'}}><div>My Surveys</div></a>
              </li>,
              <li key = "1" style={{ color: 'grey'}}>
                  Credits: {props.auth.credits}
              </li>
          ]
  }
}
else {
  switch(props.auth) {
    case null:
        return ;
    case false:
        return (
          [
            <li className="mt-2">
                <pre className="m-n2">Sign in with<b style={{fontSize: 50}}>[</b></pre>
            </li>,
            <li>
                <NavLink href="/auth/google">
                  <svg className="mt-n3" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px"
                      width="38" height="38" viewBox="0 0 48 48" style={{ fill:"#000000;"}}>
                      <path fill="#FFC107" d="M43.611,20.083H42V20H24v8h11.303c-1.649,4.657-6.08,8-11.303,8c-6.627,0-12-5.373-12-12c0-6.627,5.373-12,12-12c3.059,0,5.842,1.154,7.961,3.039l5.657-5.657C34.046,6.053,29.268,4,24,4C12.955,4,4,12.955,4,24c0,11.045,8.955,20,20,20c11.045,0,20-8.955,20-20C44,22.659,43.862,21.35,43.611,20.083z"></path>
                      <path fill="#FF3D00" d="M6.306,14.691l6.571,4.819C14.655,15.108,18.961,12,24,12c3.059,0,5.842,1.154,7.961,3.039l5.657-5.657C34.046,6.053,29.268,4,24,4C16.318,4,9.656,8.337,6.306,14.691z"></path>
                      <path fill="#4CAF50" d="M24,44c5.166,0,9.86-1.977,13.409-5.192l-6.19-5.238C29.211,35.091,26.715,36,24,36c-5.202,0-9.619-3.317-11.283-7.946l-6.522,5.025C9.505,39.556,16.227,44,24,44z"></path>
                      <path fill="#1976D2" d="M43.611,20.083H42V20H24v8h11.303c-0.792,2.237-2.231,4.166-4.087,5.571c0.001-0.001,0.002-0.001,0.003-0.002l6.19,5.238C36.971,39.205,44,34,44,24C44,22.659,43.862,21.35,43.611,20.083z"></path>
                  </svg>
                </NavLink>
            </li>,
            <li className="mt-2">
            <pre className="m-n2"><b style={{fontSize: 50}}>]</b></pre>
            </li>
            ]
        );
    default:
        return (<NavItem className="float-right" key = "2" ><a href = "/api/logout" style={{ margin: '0px 10px', color: 'grey'}}><div>Logout</div></a></NavItem>);
  }
}
}

const Example = (props) => {
  const [isOpen, setIsOpen] = useState(false);
  var isColor;

  const toggle = () => setIsOpen(!isOpen);

  return (
    <Sticky>
    { ({ style }) => (
    <header style={style}>
      <Navbar color="light" light expand="md">
        <a href="/">
          <i className="material-icons" style={{color: "grey"}}>email</i>
        </a>
        <NavbarBrand href="/">Bulk Emailer</NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="mr-auto float-left text-center" navbar>
            <NavItem>
              <a href="/about" style={{color: 'grey'}}>About</a>
            </NavItem>
            <NavItem>
              <a href="/feedback" style={{color: 'grey'}}>Contact/Feedback</a>
            </NavItem>
            {renderContent(props, true)}
            {/* <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret>
                Options
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem>
                  Option 1
                </DropdownItem>
                <DropdownItem>
                  Option 2
                </DropdownItem>
                <DropdownItem divider />
                <DropdownItem>
                  Reset
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown> */}
              
          </Nav>
        
          <Nav className="float-right">
            <NavItem>
            {renderContent(props, false)}
            </NavItem>
                    
          </Nav>
          
          
        </Collapse>
      </Navbar>
    </header>
    )
    }
    </Sticky>
  );
}

function mapStateToProps({ auth }) {
  return { auth };
}

export default connect(mapStateToProps)(Example);
